import axios from '../plugins/axios';

/**
*getget
*@param{String}url[url]
*@param{Object}data[]
*/
export function get(url, data) {
    return new Promise((resolve, reject) => {
        axios.get(url, {
            params: data
        }).then(res => {
            resolve(res.data);
        }).catch(err => {
            reject(err.data)
        })
    });
}

/**
*postpost
*@param{String}url[url]
*@param{Object}data[]
*/
export function post(url, data) {
    return new Promise((resolve, reject) => {
        axios.post(url, data)
            .then(res => {
                resolve(res.data);
            })
            .catch(err => {
                reject(err.data)
            })
    });
}
export function put(url) {
    return new Promise((resolve, reject) => {
        axios.put(url)
            .then(res => {
                resolve(res.data);
            })
            .catch(err => {
                reject(err.data)
            })
    });
}
